import React from 'react';
import {genPromise} from '../plugins';
import ResultView from './result';
import Header from './header'
class PatentItem extends React.Component{
    render(){
        return(
        <div className='card mb-3'>
            <div className='card-body'>
                <h6 className='card-title'>
                    {this.props.title}
                    <span className='text-muted'>({this.props.authorized_id})</span>
                </h6>
                <p className='card-text text-muted'>{this.props.author}</p>
            </div>
        </div>
        )
    }
}

export default class Patent extends React.Component{
    constructor(props){
        super(props);
        this.state={
            patent_list:[],
            load:false,
            error:false,
            error_msg:'error'
        }
    }

    componentDidMount(){
        genPromise('GET','http://http://39.100.113.162/info/patent/patent.php').then(
            (data)=>{
                if(data.code===0){
                    this.setState({
                        patent_list:data.data,
                        load:true
                    })
                }else{
                    this.setState({
                        error:true
                    })
                }
            },(error)=>{
                this.setState({
                    error:true,
                    error_msg:error.message
                })
            }
        )
    }

    render(){
        const patent_list=this.state.patent_list.map((el)=>
            <PatentItem key={el.id} title={el.patent_name}
                author={el.invent_name} year={el.authorized_time}
                authorized_id={el.authorized_id} />
        )
        const ContentLoader=()=>
                (<div className='content-loader p-2'>
                    <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                        style={{width:'85%',height:'.8rem'}}></div>
                    <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                        style={{width:'60%',height:'.8rem'}}></div>
                    
                </div>);
        if(!this.state.load){
            return(
                <div><Header />
                <div className='patent-container container'>
                    
                    <h4 className='title border-bottom'>专利</h4>
                    <div>
                        <ContentLoader />
                        <ContentLoader />
                        <ContentLoader />
                    </div> 
                </div></div>
            )
        }else if(this.state.error){
            return(
                <div> <Header />
                <div className='patent-container container'>
                   
                    <ResultView text={this.state.error_msg} />
                </div></div>
            )
        }
        return(
            <div> <Header />
            <div className='patent-container container'>
               
                <h4 className='title border-bottom'>专利</h4>
                <div>
                    {patent_list}
                </div>
            </div></div>
        )
    }
}